@import './config';
@import './button';

.content_container_color_light {
  background: $light-color;
  color: $text-color;
  .main_content_left {
    border: 1px solid $border-color;
  }
  .Home_Title {
    background: $light-color;
    border-bottom: 1px solid $border-color;
    a {
      svg {
        color: $secondary-color;
      }
    }
  }
  .add_post_container {
    border-bottom: 1px solid $border-color;

    &:hover {
      background: lighten($border-color, 5%);
    }
  }
}

.content_container_color_dark {
  background: $dark-color;
  color: $text-color-dark;
  .main_content_left {
    border: 1px solid $dark-border-color;
  }
  .Home_Title {
    background: $dark-color;
    border-bottom: 1px solid $dark-border-color;
    a {
      svg {
        color: $secondary-color;
      }
    }
  }
  .add_post_container {
    border-bottom: 1px solid $dark-border-color;
    &:hover {
      background: $hover-post-dark;
    }
  }
}

.content_container {
  display: flex;
  position: relative;

  //Left

  .left_container {
    display: flex;
    justify-content: flex-end;
    flex-grow: 1;

    .list_container {
      width: 270px;
      position: relative;
    }
  }
  // Right
  .right_container {
    display: flex;
    justify-content: flex-start;
    flex-grow: 1;

    .main_content {
      display: flex;
      justify-content: space-between;
      width: 990px;
      position: relative;

      .main_content_left {
        position: relative;
        min-height: 920px;
        width: 600px;
        border-top: 0;
        padding-top: 53px;
        padding-bottom: 53px;
        .Home_Title {
          top: 0;
          position: fixed;
          display: flex;
          align-items: center;
          width: 568px;
          padding: 0 1rem;
          z-index: 999;
          border-top: 0;
          height: 53px;

          a {
            display: flex;
            justify-content: center;
            align-items: center;
            padding-right: 1rem;
          }
          h3 {
            margin: 0;
            padding-top: 5px;
            font-weight: 1000;
          }
          p {
            margin: 0;
            font-size: 13px;
          }
        }
      }
    }
  }
  .no_post_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    svg {
      width: 500px;
      height: auto;
      margin-top: 1rem;
    }
    p {
      font-weight: 1000;
      font-size: 20px;
    }
  }
}
